import React from 'react';

import {Fab, Collapse, ListItemText, List, ListItem} from '@material-ui/core';
import {ExpandLess, ExpandMore} from '@material-ui/icons';
import { gameState_enum } from './enums'
import { db, key } from './firestore'

class Waiting extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }

  shuffle = (array) => {
    var currentIndex = array.length;
    var temporaryValue, randomIndex;
  
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
  
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
  
      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
  
    return array;
  }

  startGame = () => {
    var {session, players} = this.props;
  
    db.ref(`${key}/${session}`).update(
      {
        up: 0,
        order: this.shuffle(Object.keys(players)),
        gameState: gameState_enum.rolling,
      });

  }

  render() {
    // need to render the list of the current players
    // need to add a start button

    var {players} = this.props;
    var {open} = this.state;
    return (
      <div>
        <h3>Waiting for game to start</h3>
        <List>
          <ListItem button onClick={ () => this.setState({open: !open}) }>
          
            <ListItemText primary="Players" />
            {open ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <List>
              {
                Object.keys(players).map((player) => {
                  return(<ListItem key={player}>
                    <ListItemText>
                      player: {player}
                    </ListItemText>

                    <Fab variant="extended" color="secondary" aria-label="join" onClick={() => this.kickPlayer(player)} >
                      Kick Player from Game
                    </Fab>
                  </ListItem>);
                })
              }
            </List>
          </Collapse>
          <ListItem>

            <Fab variant="extended" color="primary" aria-label="join" onClick={this.startGame} >
              Start Game
            </Fab>
          </ListItem>
        </List>
      </div>
    );
  }
}
export default Waiting;
